import { createUserWithEmailAndPassword, updateProfile } from "firebase/auth";
import { useState } from "react"
import { auth } from "../firebase";
import { useNavigate } from "react-router-dom";
import { FirebaseError } from "firebase/app";
import GithubBtn from "./github-btn";
import { useDispatch } from "react-redux";
import { updateName } from "../store";

export default function Join() {
    const navigator = useNavigate();
    const dispatch = useDispatch();

    const [loading, setLoading] = useState(false);
    const [name, setName] = useState('');
    const [email, setEmail] = useState('');
    const [password, setPassword] = useState('');
    const [error, setError] = useState('');

    const onSubmit = async(e) => {
        e.preventDefault();
        setError("");
        if(loading || name === "" || email === "" || password === "") return;
        try {
            setLoading(true);
            const credentials = await createUserWithEmailAndPassword(auth, email, password);
            console.log(credentials.user);
            await updateProfile(credentials.user, {
                displayName: name,
            });
            dispatch(updateName());
            navigator("/home");
        } catch (e) {
            if(e instanceof FirebaseError){
                setError(e.message);
            }
        } finally {
            setLoading(false);
        }
        console.log(name, email, password);
    }

    return (
        <div className="login-join-form" onSubmit={onSubmit}>
            <form>
                <input name="name" value={name} placeholder="" type="text" onChange={(e)=>{setName(e.target.value)}} required />
                <input name="email" value={email} placeholder="" type="email" onChange={(e)=>{setEmail(e.target.value)}} required />
                <input name="password" value={password} placeholder="" type="password" onChange={(e)=>{setPassword(e.target.value)}} required />
                <button type="submit">{loading ? ".." : " "}</button>
            </form>
            <GithubBtn/>
            {error !== "" ? (<p className="error-txt">{error}</p>) : null}
        </div>
    )
}