import { useState } from "react"
import { auth } from "../firebase";
import { useNavigate } from "react-router-dom";
import { FirebaseError } from "firebase/app";
import { signInWithEmailAndPassword } from "firebase/auth";
import GithubBtn from "./github-btn";
import { useDispatch } from "react-redux";
import { updateName } from "../store";

export default function Join() {
    const navigator = useNavigate();
    const dispatch = useDispatch();

    const [loading, setLoading] = useState(false);
    const [email, setEmail] = useState('');
    const [password, setPassword] = useState('');
    const [error, setError] = useState('');

    const onSubmit = async(e) => {
        e.preventDefault();
        setError("");
        if(loading || email === "" || password === "") return;
        try {
            setLoading(true);
            await signInWithEmailAndPassword(auth, email, password);
            dispatch(updateName());
            navigator("/home");
        } catch (e) {
            if(e instanceof FirebaseError){
                setError(e.message);
            }
        } finally {
            setLoading(false);
        }
    }

    return (
        <div className="login-join-form" onSubmit={onSubmit}>
            <form>
                <input name="email" value={email} placeholder="guest@yung.com" type="email" onChange={(e)=>{setEmail(e.target.value)}} required />
                <input name="password" value={password} placeholder="guest123!" type="password" onChange={(e)=>{setPassword(e.target.value)}} required />
                <button type="submit">{loading ? ".." : ""}</button>
            </form>
            <GithubBtn/>
            {error !== "" ? (<p className="error-txt">{error}</p>) : null}
        </div>
    )
}