import { useNavigate } from "react-router-dom";
import { auth } from "../firebase";
import Time from "./time";
import { SlPower, SlPhone, SlBubble, SlEnvolope } from "react-icons/sl";
import { useEffect, useState } from "react";
import { useSelector } from "react-redux";
import { useDispatch } from "react-redux";
import { resizeDevice } from "../store";

export default function Layout({ children }) {
    const navigator = useNavigate();
    const dispatch = useDispatch();

    // 
    const logOut = () => {
        auth.signOut();
        navigator("/");
    }

    // 
    const isMobile = useSelector((state) => state.isMobile);
    const bp = () => {
        dispatch(resizeDevice());
        console.log(isMobile);
    };
    useEffect(() => {
        window.addEventListener('resize', bp);
        return () => {
            window.removeEventListener('resize', bp);
        };
    }, []);

    //  
    const userName = useSelector((state) => state.user);
    
    return (
        <>
            <header id="header">
                <ul className="left">
                    <li className="diff">Yung's Portfolio</li>
                    {
                        isMobile ? (
                            null
                        ) : (
                            <>
                                <li><a href="tel:010-8519-5885"></a></li>
                                <li><a href="sms:010-8519-5885"></a></li>
                                <li><a href="mailto:xodbd0507@naver.com"></a></li>
                            </>
                        )
                    }
                </ul>
                <ul className="right">
                    {isMobile ? null : <li className="log-out" onClick={logOut}><SlPower /></li>}
                    <li className="clock">{<Time/>}</li>
                </ul>
            </header>
            <div className="container">
                <p className="bg-txt">Yung's Portfolio<br/>{userName} <br/> </p>
                {children}
            </div>
            <nav id="FNB">
                <ul>
                    <li><a href="tel:010-8519-5885"><SlPhone /></a></li>
                    <li><a href="sms:010-8519-5885"><SlBubble /></a></li>
                    <li><a href="mailto:xodbd0507@naver.com"><SlEnvolope /></a></li>
                    <li><a onClick={logOut}><SlPower /></a></li>
                </ul>
            </nav>
        </>
    )
}