/* eslint-disable */
import './css/style.scss';
import { Route, Routes, useNavigate } from "react-router-dom";
import { auth } from "./firebase";
import { useEffect, useState } from "react";
import { useSelector, useDispatch } from "react-redux";
import { updateName } from "./store";
import Layout from "./component/layout";
import Intro from "./pages/intro";
import Home from "./pages/home";
import Kty from "./pages/kty";
import Work from "./pages/work";
import Game from "./pages/game";
import GiftDraw from "./component/game/giftDraw";
import Quiz from "./component/game/quiz";
import PageLayout from "./component/pageLayout";

export default function App() {
  const navigator = useNavigate();
  const dispatch = useDispatch();

  // 
  const [loading, setLoading] = useState(true);

  // 
  const [isLogin, setIsLogin] = useState(false);

  const init = async () => {
    await auth.authStateReady();
    setLoading(false);
    if(auth.currentUser == null) {
      navigator("/");
    } else {
      setIsLogin(true);
      dispatch(updateName());
      console.log(auth.currentUser);
    }
  }
  useEffect(() => {
    init();
  }, []);

  //  100vh
  function setScreenSize() {
    let vh = window.innerHeight * 0.01;
    document.documentElement.style.setProperty("--vh", `${vh}px`);
  }
  useEffect(() => {
    setScreenSize();
  });

  return (
    <>
      {loading ? (
        <div className="loading-wrap">LOADING...</div>
      ) : (
        <Routes>
          <Route path="/" element={<Intro/>}></Route>
          <Route path="/home" element={<Layout><Home/></Layout>}></Route>
          <Route path="/kty" element={<Layout><PageLayout pageName={`KTY`}><Kty/></PageLayout></Layout>}></Route>
          <Route path="/work" element={<Layout><PageLayout pageName={`Work`}><Work/></PageLayout></Layout>}></Route>
          <Route path="/game" element={<Layout><PageLayout pageName={`Game`}><Game/></PageLayout></Layout>}>
            <Route path="quiz" element={ <Quiz /> } />
            <Route path="giftDraw" element={ <GiftDraw /> } />
          </Route>
          <Route path="*" element={<div className='error-404'>404 Not Found</div>} />
        </Routes>
      )}
    </>
  );
}