import { useEffect, useRef, useState } from "react";
import gsap from "gsap";
// import Folder from "../component/folder"
import { Draggable } from "gsap/Draggable";
import { useSelector } from "react-redux";
import Popup from "../component/popup";

gsap.registerPlugin(Draggable);


export default function Home() {
    const isMobile = useSelector((state) => state.isMobile);

    //  + 
    const folder = useSelector((state) => {return state.folder});
    const folderRefs = useRef([]);
    const popupRef = useRef(null);  
    useEffect(() => {
        folderRefs.current.forEach((ref) => {
            Draggable.create(ref, {
                bounds: ".home-wrap",
                inertia: true,
            });
        });

        Draggable.create(popupRef.current, {
            bounds: ".home-wrap",
            inertia: true,
        });
        
        return () => {
            folderRefs.current.forEach((ref) => {
                Draggable.get(ref)?.kill();
            });
            Draggable.get(popupRef.current)?.kill();
        };
    }, []);

    //  
    const [popOpen, setPopOpen] = useState(false);

    const [fileList, setFileList] = useState(folder[0]);

    const openFolder = (idx) => {
        setPopOpen(true);
        setFileList(folder[idx]);
    };  
    const closeFolder = (state) => {
        setPopOpen(state);
    }
    
    
    return (
        <div className="home-wrap">
            <ul className="folder-list">
            {
                folder.map((item, idx) => (
                    <li key={idx} ref={(el) => (folderRefs.current[idx] = el)}>
                        <div 
                            className="folder-wrap" 
                            onClick={isMobile ? () => openFolder(idx) : undefined}
                            onDoubleClick={!isMobile ? () => openFolder(idx) : undefined}
                        >
                            <div className="folder-top"></div>
                            <div className="folder"></div>
                            <p>{item.fName}</p>
                        </div>
                    </li>
                ))
            }
            </ul>
            <Popup open={popOpen} close={closeFolder} fileList={fileList} dragRef={!isMobile ? popupRef : undefined}/>
        </div>
    );
}