import { useNavigate } from "react-router-dom";

export default function PageLayout({ children, pageName }) {
    const navigate = useNavigate();

    return (
        <>
            <div className="browser">
                <div className="browser-head popup-head">
                    <div className="popup-close" onClick={()=>navigate("/home")}></div>
                    <div className="popup-hidden"></div>
                    <div className="popup-size"></div>
                    <p>{pageName}</p>
                </div>
                <div className="browser-body">
                    {children}
                </div>
            </div>
        </>
    )
}