import { useState } from "react"
import { useSelector } from "react-redux";
import { Link } from "react-router-dom";

export default function Popup(props) {
    const isMobile = useSelector((state) => state.isMobile);

    const [size, setSize] = useState("");

    const sizeClick = () => {
        if(size === "full") {
            setSize("");
        }else {
            setSize("full");
        }
    }

    const fileList = props.fileList;
    console.log(fileList);

    return (
        <div className={`popup-wrap ${size} ${props.open ? "" : "displaynone"}`} ref={props.dragRef}>
            <div className="popup-head" onDoubleClick={!isMobile ? ()=>sizeClick() : undefined}>
                <div className="popup-close" onClick={()=>props.close(false)}></div>
                <div className="popup-hidden"></div>
                <div className="popup-size" onClick={!isMobile ? ()=>sizeClick() : undefined}></div>
                <p>{fileList.fName}</p>
            </div>
            <div className="popup-body">
                <ul className="file-list">
                    {
                        fileList.file.map((item, idx) => (
                            <li key={idx}>
                                <Link to={item.link} target={item.target} className="file-wrap" title={item.txt}>
                                    <div className="file"></div>
                                    <p>{item.txt}</p>
                                </Link>
                            </li>
                        ))
                    }
                </ul>
            </div> 
        </div>
    )
}