import { configureStore, createSlice } from '@reduxjs/toolkit'
import { auth } from './firebase'

//  
let user = createSlice({
    name: "user",
    initialState: "user",
    reducers: {
        updateName(){
            return auth.currentUser.displayName;
        }
    }
});
export let {updateName} = user.actions;

// 
let isMobile = createSlice({
    name: "isMobile",
    initialState: window.innerWidth < 490,
    reducers: {
        resizeDevice(){
            return window.innerWidth < 490;
        }
    }
});
export let {resizeDevice} = isMobile.actions;

