import { useEffect, useState } from "react";
import { SlUser } from "react-icons/sl";
import { SlUserFollow } from "react-icons/sl";
import Login from "../component/login";
import Join from "../component/join";
import Time from "../component/time";

export default function Intro() {
    const [intro, setIntro] = useState('');
    const [fadeOut, setFadeOut] = useState('');
    const [fadeIn, setFadeIn] = useState('');
    const [howAccount, setHowAccount] = useState('login');

    useEffect(()=>{
        setTimeout(()=>{setIntro('on')}, 1000);
        setTimeout(()=>(setFadeOut('fadeout'), setFadeIn('fadein')), 3300);
        return ()=>{
            setIntro('');
            setFadeOut('');
            setFadeIn('');
        }
    }, []);

    function choose() {
        if(howAccount === "login") {
            return <Login/>;
        } else if(howAccount === "join"){
            return <Join/>;
        }
    };

    return (
        <>
            <div className={`intro-wrap ${fadeOut}`}>
                <p className="intro-logo"></p>
                <h1>Yung's portfolio</h1>
                <div className={`loading-bar ${intro}`}></div>
            </div>
            <div className={`account-wrap ${fadeIn}`}>
                <div className="guest-auth">
                    <p> </p>
                    <p>ID: guest@yung.com</p>
                    <p>PW: guest123!</p>
                </div>
                <div className={`top-area`}>
                    <Time/>
                </div>
                <div className={`bot-area`}>
                    <div className="account-btn">
                        <button className="login-account" onClick={()=>{setHowAccount("login")}}><SlUser /></button>
                        <button className="create-account" onClick={()=>{setHowAccount("join")}}><SlUserFollow /></button>
                    </div>
                    {choose()}
                </div>
            </div>  
        </>
    )
}